import uniRate from '@/components/uni-rate/uni-rate.vue'
var sourceType = [
		['camera'], //拍照
		['album'], //相册
		['camera', 'album'] //拍照或相册
	]
	var sizeType = [
		['compressed'], //压缩
		['original'], //原图
		['compressed', 'original'] //压缩或原图
		]
export default{
	components:{
		uniRate
	},
	data(){
		return {
			data:'',
			form:{
				uid:'',
				token:'',
				orderid:'',
				content:'',
				isanonymous:'0',
				star:'',
				pic:[]
			},
			current:0,
			imageList: [], //保存图片路径集合
			imageLength: 5, //限制图片张数
			sourceTypeIndex: 2, //添加方式限制
			sizeTypeIndex: 2, //图片尺寸限制
		}
	},
	onLoad(p){
		var that=this
		var userInfo=uni.getStorageSync('userInfo'),that=this
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.form.uid=userInfo.uid
			this.form.token=userInfo.token
		}
		var info=JSON.parse(p.item)
		this.data=info
		this.form.orderid=info.orderno
	},
	methods:{
		starChange(e){
			this.form.star=e.value
		},
		//选择图片
		chooseImage: async function() {
				uni.chooseImage({
					sourceType: sourceType[this.sourceTypeIndex],

					sizeType: sizeType[this.sizeTypeIndex],

					count: this.imageLength - this.imageList.length,
					success: (res) => {
						this.imageList = this.imageList.concat(res.tempFilePaths);
					}
				})
			},
			//预览图片
			previewImage: function(e) {
				var current = e.target.dataset.src
				uni.previewImage({
					current: current,
					urls: this.imageList
				})
			},
			//删除图片
			deleteImage: function(e) {
				var index = e.target.dataset.index;
				var that = this;
				var images = that.imageList;
				images.splice(index, 1);
				that.imageList = images;
			},
			radioChange(e){
				console.log(e)
				this.form.isanonymous=e.detail.value
			},
			submit(){
				var  that=this,l=this.imageList.length;
				this.$loading()
				if(l==0){
					that.submitForm()
				}else{
					this.imageList.forEach((item,index) =>{
						that.$upload(item,'',(cb) =>{
							that.form.pic.push(cb)
						var	t=this.form.pic.length
							if(l==t){
								that.submitForm()
							}
						})
					})
				}
				
			},
			submitForm(){
				var that=this
				var url='&r=api.member.order.commentAdd'
				  this.$apiPost(url,this.form).then((res) =>{
						that.$msg('评价成功')
						uni.hideLoading()
						setTimeout(function(){
							uni.navigateBack({
								delta:1
							})
						},500)
				  })
			}
	}
}
